@import '~antd/dist/antd.css';

.App-layout {
  min-height    : 100vh;
  display       : flex;
  flex-direction: column;
}

.App-header {
  flex: 0 0 auto;
}

.App-body {
  flex   : 1 0 auto;
  display: flex;
}

.App-route {
  flex-grow : 1;
  margin-top: 35px;
}

.App-footer {
  flex: 0 0 auto;
}